<template>
<div>
  <div class="hello">
    <scroller :on-refresh="refresh" :on-infinite="infinite" ref="myscroller">
    <Header></Header>
      <van-swipe :autoplay="3000" class="swipe">
        <van-swipe-item></van-swipe-item>
        <van-swipe-item></van-swipe-item>
        <van-swipe-item></van-swipe-item>
        <van-swipe-item></van-swipe-item>
      </van-swipe>    
      <ul class="content" v-for="(item, index) in response" :key="index">
        <li id="linkto" @click="linkto(item.id)">{{item.title}}</li>
        <img :src='item.images[0]' >
      </ul>
    </scroller>
  </div>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Lazyload } from 'vant';
import VueScroller from 'vue-scroller'
import vue from 'vue';
vue.use(Swipe).use(SwipeItem).use(Lazyload).use(VueScroller);
import Header from './Header'
export default {
  name: 'HelloWorld',
  components:{
    Header: Header
  },
  data () {
    return {
        response: [],
        time: 1
    }
  },
  created() {
     this.getData();
          },
  methods:{
    getData(){
      this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/last-stories',          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories)
                console.log(res)
            })
    },

    beforeData(){
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth()+1;
      var day = date.getDate();
      console.log(date.getDate())
      if(month<9){
        month = '0' + month; 
      }
      if(day <9){
        day = '0' + day;
      }
      var before = year+''+month+day - this.time;
      console.log(before)
      if(before != '20190601'){
        this.$http({
                method: 'get',
                url:'https://zhihu-daily.leanapp.cn/api/v1/before-stories/'+before,          
            }).then(res=>{
                this.response = this.response.concat(res.data.STORIES.stories);
                this.time ++;
          })
      }
      else{
        console.log('20190601')
      }
      
    },
    linkto(id){
      this.$router.push({path:'/details/'+id})
    },
    infinite(done) {
    
      var vm = this;
      setTimeout(()=>{
          vm.beforeData();
          done();
        },1000)
    },
    refresh() {
      this.response =[];
      this.time = 1;
      setTimeout(() => {
        this.getData();
        this.$refs.myscroller.finishPullToRefresh();
      }, 500);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello{
  overflow-y: scroll;
  touch-action: pan-y;
  -webkit-overflow-scrolling: touch;
}
#linkto{
  cursor: pointer;
}
#linkto:hover{
  color: orange;
}
.swipe{
  width: 100%;
  height: 3rem;;
  background: pink
}
li{
  font-size: 0.35rem;
}
.content{
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
  padding-left: 0.2rem;
  padding-right: 0.2rem;
}
.content li{
  padding-top: 0.5rem;
}
.content >>>img{
  width: 2rem;
  height: 2rem;
}
</style>



